@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  padding:0;
  margin:0;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  background-color: #3f3f3f;
  background-image: url(/Food-Ordering-app/static/media/8407002.595992e4.png);
  background-repeat: no-repeat;
  background-size: 100% 700px;
  background-position: fixed;
}


.checkout{
    background-color:white;
    width:50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 2rem;
    box-shadow: 5px 5px 10px;
    position: fixed;
    top: 40%;
    left: 50%;
    width:20%;
    transform: translate(-50%, -30%);
    z-index: 20; 

}
h1{
    color:rgb(241, 136, 7)
}
.form{
    padding: 1rem;
}
.input{
    height:2rem;
    border-radius:1rem;
    font-size: 1rem;
    text-align: center;
    outline:none;
    border-color:black;
    
    
}
.input:focus{
    background-color:rgb(245, 219, 184);
    border-color:rgb(236, 153, 44);
   

}
.area{
    height: 5rem;
}
.confirm{
    background-color:rgb(236, 153, 44);
    color:white;
    border:none;
    border-radius: 2rem;
    padding:1rem;
    cursor:pointer;
   
    margin-top:1rem;
    &:hover{
        background-color:white;
        color:rgb(236, 153, 44);
        border:1px solid rgb(236, 153, 44);
    }
}
.cancel{
    background-color:white;
    color:rgb(236, 153, 44);
    border:1px solid rgb(236, 153, 44);
    border-radius: 2rem;
    padding:1rem;
    margin-left:1rem;
    cursor:pointer;
    &:hover{
        background-color:rgb(243, 140, 5);
        color:white;
        
    }

}
input::-webkit-outer-spin-button, input::-webkit-inner-spin-button { -webkit-appearance: none; margin: 0; }
.cart{
    display: flex;
    align-items: center;
    justify-content: center;
    color:white;
    font-size:1rem;
    grid-gap:0.4rem;
    gap:0.4rem;
}
.container{
    display: flex;
    justify-content: center;
    background-color:rgb(73, 35, 17);
    border-radius:5rem;
    height: 2.5rem;
    width:13rem;
    margin-right: 2rem;
    cursor: pointer;
}
.count{
    display:flex;
    background-color: rgb(194, 61, 28);
    width:3rem;
    height: 2rem;
    border-radius: 2rem;
    justify-content: center;
    align-items: center;
}
.header{
    width:100%;
    display:flex;
    justify-content:space-between;
    align-items: center;
    background-color: #a84444;
    position: fixed;
    top:0;
}
.title{
    font-family:sans-serif;
    font-weight: bolder;
    color:white;
    font-size: 3rem;
    margin-left: 2rem;
   
}
.msg{
    color:white;
    background-color:#3f3f3f;
    width:40%;
    text-align: center;
    border-radius:1rem;
    padding:1rem;
    margin-left:67vh;
    margin-top:70vh;
    box-shadow: 5px 5px 10px black;
}

.details{
    width:100%;
    .name{
        color:black;
        font-size: 1rem;
        line-height: 1rem;
        font-weight: bold;
    }
    .dec{
        font-style: italic;
    }
    .price{
        color:rgb(172, 98, 2);
        font-weight: bold;
    }
}
.contain{
    width:100vh;
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin-top:1.5rem;
    margin-bottom: 1rem;
}
hr{
    width:90%;
}
.addtocart{
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap:0.8rem;
    gap:0.8rem;
    font-weight:bolder;
}
.amt{
    width:2rem;
    height: 1.5rem;
    outline:none;
}
.add{
    width:6rem;
    height: 2rem;
    background-color: rgb(172, 98, 2);
    margin-top:0.5rem;
    margin-left:0.5rem;
    outline:none;
    border:none;
    border-radius: 2rem;
    font-weight:bolder;
    color:white;
    cursor:pointer;
}
.menu{
    width:110vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: white;
    border-radius: 2rem;
    box-shadow: 5px 5px 10px;
    padding: 1rem;
}
.menucard{
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin:2rem;
    
}
.left{
    display:flex;
    color:rgb(172, 98, 2);
    grid-gap:2rem;
    gap:2rem;
}

li{
    list-style: none;
}
.selected-items{
    display:flex;
    justify-content: space-between;
    align-items: center;
}
.quantity{
    background-color:white;
    border:1px solid rgb(172, 98, 2);
    width:3rem;
    border-radius:1rem;
    color:rgb(172, 98, 2);
    margin-right:1rem;
    font-weight: bolder;
    cursor: pointer;
}
hr{
    width :100%;
    margin:0.5rem;
}
/* Backdrop.css */
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5); /* semi-transparent black */
    z-index: 10; /* keep it on top */
  }
/* Modal.css */
.cartpopup {
    position: fixed;
    top: 30%;
    left: 50%;
    width:30%;
    transform: translate(-50%, -30%);
    background: white;
    padding: 1rem;
    border-radius: 8px;
    z-index: 20; /* higher than backdrop */
    box-shadow: 0 2px 8px rgba(0,0,0,0.26);
  }
    
/* .cartpopup{
    width:30%;
    background-color: white;
    border-radius: 1rem;
    box-shadow: 5px 5px 10px;
    padding: 1rem;

} */
.total{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-right: 3rem;
}
.buttons{
    display:flex;
    justify-content: flex-end;
    grid-gap: 1rem;
    gap: 1rem;
    margin-right:2rem;
    
    .order{
        background-color: rgb(172, 98, 2);
        color:white;
        border:none;
        border-radius: 2rem;
        padding:1rem;
        cursor:pointer;
        font-weight: bolder;
    }
    .close{
        border:1px solid rgb(172, 98, 2);
        background-color: white;
        color:rgb(172, 98, 2);
        border-radius: 2rem;
        padding:1rem;
        cursor:pointer;
        font-weight: bolder;
    }
}

